.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  color: white;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#main-header {
  display: flex;
  border-bottom: 3px solid #20232b;
}

#main-header img {
  min-height: 50px;
  height: 5vh;
  margin-top: auto;
  margin-bottom: auto;
}

#shopping-cart {
  height: 50px;
  width: 50px;

  position: absolute;
  top: 2vh;
  right: 3vw;

  user-select: none;
  -moz-user-select: none;
}

.centered {
  padding-top: 1em;
  display: flex;
  justify-content: center;
  text-align: center;
}

.products {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;

  max-width: 70vw;
}

.product-card {
  background-color: #212738;
  padding: 10px;
  border-radius: 1em;

  max-width: 20vw;
  min-width: 200px;

  margin-top: 10px;

  box-shadow: #07112e 0px 5px 10px;
}

.product-description {
  margin: auto;
  width: 60%;
}

.product-bottom {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

#cart-items {
  display: inline-block;
  background-color: #5a53bd;
  height: 30vh;
  width: 250px;
  position: absolute;
  top: 75px;
  right: 0px;
  border-radius: 10px;
  border: 2px solid black;
  box-shadow: #313e64 4px 2px 10px;

  display: flex;
  flex-direction: column;
  padding: 10px 0px 10px 0px;
}

.cart-item {
  margin-left: 10px;
  display: inline-block;
}

.cart-item-qty {
  display: inline-block;
  position: absolute;
  right: 35px;

  font-weight: bold;
}

.cart-item-add {
  margin-right: 4px;
}

.cart-item-sub {
  margin-left: 4px;
}

#main-footer {
  margin-top: 50px;
  border-top: 2px solid black;
  min-height: 100px;
}